import React from 'react';
import ReactDOM from 'react-dom';
import Turn from './Turn.js';
import './styles.css';

const pages = [
  'https://cdn1.ozone.ru/multimedia/1032646624.jpg',
  'img/1.png',
  'img/2.png',
  'img/3.png',
  'img/4.png',
  'img/5.png',
  'img/6.png',
  'img/7.png',
  'img/8.png',
  'img/9.png',
  'img/10.png',
  'img/11.png',
  'img/12.png',
  'img/13.png',
  'img/14.png',
  'img/15.png',
  'img/16.png',
  'img/17.png',
  'img/18.png',
  'img/19.png',
  'img/20.png',
  'img/21.png',
  'img/22.png',
  'img/23.png',
  'img/24.png',
  'https://cdn1.ozone.ru/multimedia/1032646624.jpg',
];

const App = () => {
  return (
    <Turn className="book">
      {pages.map((page, index) => {
        const className = `page${
          index === 0 ||
          index === 1 ||
          index === pages.length - 1 ||
          index === pages.length - 2
            ? ' hard'
            : ''
        }`;

        return (
          <div key={index} className={className}>
            <img src={page} alt={index} />
          </div>
        );
      })}
    </Turn>
  );
};

const rootElement = document.getElementById('root');

ReactDOM.render(<App />, rootElement);
