import React, { useRef, useEffect, useState } from 'react';
import '@ksedline/turnjs';
import $ from 'jquery';

const Turn = ({ style, className, children }) => {
  const options = {
    width: 1000,
    height: 650,
    autoCenter: true,
    display: 'double',
    acceleration: true,
    elevation: 50,
    gradients: !$.isTouch,
    when: {
      turned: function (e, page) {},
    },
  };

  let fadeClass = useRef('');

  useEffect(() => {
    if (fadeClass) {
      const $Turn = $(fadeClass);

      $Turn.turn(Object.assign({}, options));
    }
  }, []);

  return (
    <div
      className={className}
      style={Object.assign({}, style)}
      ref={(el) => {
        fadeClass = el;
      }}
    >
      {children}
    </div>
  );
};

export default Turn;
